/*Angular host must stretch */
:host {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/*Fix PrimeNG DataView internals */
:host ::ng-deep .p-dataview {
  display: flex;
  flex-direction: column;
  height: 100%;
}
:host ::ng-deep .p-dataview-content {
  flex: 1;
  display: flex;
  overflow: hidden;
}
