:host ::ng-deep {
  /* Day cell stretching */
}
:host ::ng-deep .day-cell {
  display: flex;
  flex-direction: column;
  border-radius: 0;
  padding: 0.2rem;
  transition: all 0.2s ease-in-out, box-shadow 0.15s ease;
  height: 100%;
}
:host ::ng-deep .day-cell:hover {
  background-color: color-mix(in srgb, var(--p-primary-color) 30%, transparent);
  box-shadow: inset 0 0 0 1px var(--p-primary-200), 0 1px 2px rgba(0, 0, 0, 0.06);
}
:host ::ng-deep .day-cell.today {
