  box-shadow: inset 0 0 0 2px color-mix(in srgb, var(--p-primary-color) 25%, transparent);
}
:host ::ng-deep .p-card {
  display: flex;
  flex-direction: column;
}
:host ::ng-deep .p-card-body {
  flex: 1;
  padding: 0;
}
:host ::ng-deep .out-month {
  opacity: 0.45;
}
:host ::ng-deep .income-calendar-item,
